
@font-face{font-family:'Bodoni';font-style:normal;font-weight:400;src:url(./assets/font/Bodoni/Bodoni.eot);src:local("Bodoni"),url(./assets/font/Bodoni/Bodoni.eot?#iefix) format("embedded-opentype"),url(./assets/font/Bodoni/Bodoni.woff) format("woff"),url(./assets/font/Bodoni/Bodoni.ttf) format("truetype"),url(./assets/font/Bodoni/Bodoni.svg) format("svg")
