
@font-face{font-family:'Adele-light-webfont';src:url("../assets/font/adele-light-webfont/adele-light-webfont.eot");src:local("☺"),url("../assets/font/adele-light-webfont/adele-light-webfont.woff") format("woff"),url("../assets/font/adele-light-webfont/adele-light-webfont.ttf") format("truetype"),url("../assets/font/adele-light-webfont/adele-light-webfont.svg") format("svg");font-weight:normal;font-style:normal
