.about-card{font-family:'Adele-light-webfont', -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, "Helvetica Neue", Helvetica, "PingFang SC", "Hiragino Sans GB", sans-serif;font-weight:300;font-style:normal;line-height:1.4;font-size:1.5rem;margin:0 auto;width:995px;text-align:center;padding:0 0 32px
