
.pic{position:relative;background:#f4f4f4;line-height:0
}
.pic img{display:block;vertical-align:top;opacity:0;filter:alpha(opacity=0)
}
.pic img.show{-webkit-transition:all 500ms cubic-bezier(0.445, 0.05, 0.55, 0.95);-o-transition:all 500ms cubic-bezier(0.445, 0.05, 0.55, 0.95);transition:all 500ms cubic-bezier(0.445, 0.05, 0.55, 0.95);opacity:1;filter:alpha(opacity=100)
}
@-webkit-keyframes scroll_down{
0%{opacity:0;-webkit-transform:rotate(-45deg) translate3d(40px, -20px, 0);transform:rotate(-45deg) translate3d(40px, -20px, 0)
}
30%{opacity:1
}
70%{opacity:1
}
100%{opacity:0;-webkit-transform:rotate(-45deg) translate3d(0, 20px, 0);transform:rotate(-45deg) translate3d(0, 20px, 0)
}
}
@-o-keyframes scroll_down{
0%{opacity:0;transform:rotate(-45deg) translate3d(40px, -20px, 0)
}
30%{opacity:1
}
70%{opacity:1
}
100%{opacity:0;transform:rotate(-45deg) translate3d(0, 20px, 0)
}
}
@keyframes scroll_down{
0%{opacity:0;-webkit-transform:rotate(-45deg) translate3d(40px, -20px, 0);transform:rotate(-45deg) translate3d(40px, -20px, 0)
}
30%{opacity:1
}
70%{opacity:1
}
100%{opacity:0;-webkit-transform:rotate(-45deg) translate3d(0, 20px, 0);transform:rotate(-45deg) translate3d(0, 20px, 0)
}
