.work-item{position:relative;padding-bottom:120px
}
.work-item.ov .info .mark{opacity:1;filter:alpha(opacity=100)
}
.work-item.open .info .mark.mark-plus{visibility:hidden;opacity:0;filter:alpha(opacity=0)
}
.work-item.open .info .mark.mark-minus{visibility:visible
}
.work-item.rev .info{position:absolute;left:auto;right:0px;text-align:right
}
.work-item.rev .info.fixed{right:50%;margin-left:0px;margin-right:-497.5px
}
.work-item.rev .info .mark{position:absolute;left:auto;right:-45px
}
.work-item.rev .info h3{margin-left:0px;float:right
}
.work-item.rev .info h3 .inr{left:auto;right:12px
}
.work-item.rev .info .date{clear:both
}
.work-item.rev .info .desc{float:right
}
.work-item.rev .info .desc .inr{left:auto;right:12px
}
.work-item.rev .picArea{margin-right:195px;margin-left:0px
}
.work-item.rev .teaser{*zoom:1
}
.work-item.rev .teaser:after{content:".";display:block;clear:both;height:0;visibility:hidden;overflow:hidden
}
.work-item.rev .teaser.abs{left:auto;right:0;top:0
}
.work-item.rev .teaser .pic{float:right
}
.work-item.rev .listWrap .list .row{*zoom:1
}
.work-item.rev .listWrap .list .row:after{content:".";display:block;clear:both;height:0;visibility:hidden;overflow:hidden
}
.work-item.rev .listWrap .list .row .pic{float:right
}
.work-item .info{position:absolute;left:0px;top:0px;margin-top:100px
}
.work-item .info .mark{position:absolute;left:-40px;top:9px;width:40px;height:40px;line-height:1.05;text-align:center;font-style:normal;font-weight:100;font-size:4.0625rem;cursor:default;opacity:0;filter:alpha(opacity=0);-webkit-transition:all 200ms cubic-bezier(0.165, 0.84, 0.44, 1);-o-transition:all 200ms cubic-bezier(0.165, 0.84, 0.44, 1);transition:all 200ms cubic-bezier(0.165, 0.84, 0.44, 1)
}
.work-item .info .mark.mark-plus{font-size:3.4375rem
}
.work-item .info .mark.mark-minus{margin-top:-38px;visibility:hidden
}
.work-item .info .infoClip{position:relative;overflow:hidden
}
.work-item .info .infoInr{position:relative
}
.work-item .info .black{color:#16161f !important
}
.work-item .info .black *{color:#16161f !important
}
.work-item .info.info-white{height:0
}
.work-item .info.info-white *{color:#fff
}
.work-item .info.info-white .mark{display:none
}
.work-item .info.info-white .date{visibility:hidden
}
.work-item .info.fixed{position:fixed;left:50%;margin-left:-497.5px
}
.work-item .info h3{position:relative;font-size:2.625rem;font-family:"BodoniStd-Italic", "Source Han Sans", "思源黑体", "Hiragino Sans GB", "冬青黑体", "Microsoft YaHei", "-apple-system", "BlinkMacSystemFont", "Segoe UI", "Roboto", "Helvetica Neue", "Helvetica", "PingFang SC", "Hiragino Sans GB", "Microsoft YaHei", "SimSun", serif;line-height:1;font-weight:400;font-style:italic;margin-bottom:10px;margin-top:0
}
.work-item .info h3 .inr{position:absolute;left:0;top:0;white-space:nowrap
}
.work-item .info h3 a{color:#16161f
}
.work-item .info .date{font-size:.75rem;font-style:italic;padding-bottom:5px
}
.work-item .info .desc{position:relative;font-size:.75rem;font-style:italic;padding-bottom:10px
}
.work-item .info .desc .inr{position:absolute;left:0;top:0;white-space:nowrap
}
.work-item .picArea{position:relative;margin-left:195px
}
.work-item .teaser{position:relative
}
.work-item .teaser.abs{position:absolute;left:0;top:0
}
.work-item .teaser.hide{display:none;-webkit-transition:all 500ms cubic-bezier(0.445, 0.05, 0.55, 0.95);-o-transition:all 500ms cubic-bezier(0.445, 0.05, 0.55, 0.95);transition:all 500ms cubic-bezier(0.445, 0.05, 0.55, 0.95);opacity:0;filter:alpha(opacity=0);visibility:hidden
}
.work-item .teaser a .pic.hide{-webkit-transition:all 900ms cubic-bezier(0.445, 0.05, 0.55, 0.95);-o-transition:all 900ms cubic-bezier(0.445, 0.05, 0.55, 0.95);transition:all 900ms cubic-bezier(0.445, 0.05, 0.55, 0.95);opacity:0;filter:alpha(opacity=0);visibility:hidden
}
.work-item .teaser .black{position:absolute;left:0;top:0;width:100%;height:100%;background:#16161f;opacity:0;filter:alpha(opacity=0);visibility:hidden;-webkit-transition:all 900ms cubic-bezier(0.445, 0.05, 0.55, 0.95);-o-transition:all 900ms cubic-bezier(0.445, 0.05, 0.55, 0.95);transition:all 900ms cubic-bezier(0.445, 0.05, 0.55, 0.95)
}
.work-item .teaser .black.show{-webkit-transition:all 400ms cubic-bezier(0.445, 0.05, 0.55, 0.95);-o-transition:all 400ms cubic-bezier(0.445, 0.05, 0.55, 0.95);transition:all 400ms cubic-bezier(0.445, 0.05, 0.55, 0.95);opacity:1;filter:alpha(opacity=100);visibility:visible
}
.work-item .teaser .picLoadingCenter{position:absolute;width:90px;height:66px;left:50%;top:50%;margin-left:-45px;margin-top:-33px
}
.work-item .teaser .picLoadingCenter.show{opacity:1;filter:alpha(opacity=1)
}
.work-item .teaser .picLoadingCenter.hide{-webkit-transition:all 300ms cubic-bezier(0.445, 0.05, 0.55, 0.95);-o-transition:all 300ms cubic-bezier(0.445, 0.05, 0.55, 0.95);transition:all 300ms cubic-bezier(0.445, 0.05, 0.55, 0.95);opacity:0;filter:alpha(opacity=0)
}
.work-item .teaser .scrollArw.show{position:absolute;left:50%;top:50%;margin-left:-30px;margin-top:-30px
}
.work-item .teaser .scrollArw.show div{width:30px;height:30px;border-left:1px solid white;border-bottom:1px solid white;-webkit-animation:scroll_down .8s ease-in-out infinite;-o-animation:scroll_down .8s ease-in-out infinite;animation:scroll_down .8s ease-in-out infinite
}
.work-item .teaser .scrollArw.hide{-webkit-transition:all 600ms cubic-bezier(0.445, 0.05, 0.55, 0.95);-o-transition:all 600ms cubic-bezier(0.445, 0.05, 0.55, 0.95);transition:all 600ms cubic-bezier(0.445, 0.05, 0.55, 0.95);opacity:0;filter:alpha(opacity=0)
}
.work-item .listWrap{position:relative;height:0;overflow:hidden
}
.work-item .listWrap .list{position:relative;display:none
}
.work-item .listWrap .list.show{display:block
}
.work-item .listWrap .list .row{padding-bottom:100px
}



