
.siteLoading-open{overflow:hidden
}
.siteLoading{position:fixed;left:0;top:0;width:100%;height:100%;background:#ffffff;z-index:1
}
.siteLoading .siteLoadingCenter{position:absolute;width:130px;height:94px;left:50%;top:50%;margin-left:-65px;margin-top:-47px
}
.siteLoading.hide{opacity:0;filter:alpha(opacity=0);visibility:hidden;-webkit-transition:all 300ms cubic-bezier(0.165, 0.84, 0.44, 1);-o-transition:all 300ms cubic-bezier(0.165, 0.84, 0.44, 1);transition:all 300ms cubic-bezier(0.165, 0.84, 0.44, 1)
}



